* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.center {
  width: 100%;
  padding: 0 15px;
}
@media (min-width: 1200px) {
  .center {
    margin: 0 auto;
    width: 1170px;
  }
}

.button {
  color: #fff;
  display: block;
  font-size: 24px;
  font-weight: 400;
  text-decoration: none;
  padding: 10px 30px;
  background-color: #22A39F;
  border-color: #1c8784;
  border-radius: 4px;
  text-align: center;
  width: 300px;
  margin-top: 30px;
  transition: background-color 0.5s, opacity 0.5s;
}
.button:hover {
  background-color: #1c8784;
}
.button:active {
  opacity: 30%;
}

.inner-page-title {
  color: #727878;
  font-size: 54px;
  font-weight: 400;
  margin-bottom: 40px;
  margin-top: 40px;
  text-align: center;
}

.content .box input {
  border: 1px solid #a0a0a0;
  background-color: #f5f5f5;
  width: 457px;
  height: 34px;
  padding-left: 10px;
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 12px;
}

.swiper-section {
  margin-bottom: 40px;
}

.swiper {
  width: 600px;
  height: 300px;
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: lighter;
  font-display: swap;
  src: url("../../fonts/opensanslight.woff2") format("woff2"), url("../../fonts/opensanslight.woff") format("woff");
}
.page {
  height: 100%;
}

.page-body {
  margin: 0;
  padding: 0;
  background-color: #f3efe0;
  color: #434242;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 24px;
  line-height: 1.64;
  font-weight: 400;
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  min-height: 100vh;
  min-width: 320px;
}

.header {
  position: relative;
  width: 100%;
  z-index: 2;
  background-color: #22a39f;
  color: #fff;
  line-height: 1.2;
  padding: 30px 80px 30px 30px;
  overflow: hidden;
}

@media (min-width: 1200px) {
  .header {
    min-height: 150px;
    padding: 0;
  }
}
.header-wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}

@media (min-width: 1200px) {
  .header-wrapper {
    padding-top: 30px;
    padding-bottom: 30px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header-logo {
  display: flex;
  flex-direction: column;
}

.header-logo p {
  font-size: 26px;
  font-weight: 300;
  color: #fff;
}

.header-logo h1, .header-logo p {
  margin: 0;
  padding: 0;
}

.header-logo h1 {
  font-size: 36px;
}

.header-toggle {
  position: absolute;
  background-color: transparent;
  border: 0;
  right: 30px;
  top: 30px;
  padding: 20px 0;
}

@media (min-width: 1200px) {
  .header-toggle {
    display: none;
  }
}
.header-toggle span {
  display: block;
  width: 40px;
  height: 3px;
  position: relative;
}

.header--nav-closed .header-toggle span {
  background-color: #fff;
}

.header--nav-closed .header-toggle span::after, .header--nav-closed .header-toggle span::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transition: transform 0.4s;
}

.header--nav-closed .header-toggle span::before {
  display: block;
  transform: translateY(-10px);
}

.header--nav-closed .header-toggle span::after {
  left: 0;
  transform: translateY(10px);
}

.header--nav-opened .header-toggle span {
  background-color: transparent;
}

.header--nav-opened .header-toggle span::after, .header--nav-opened .header-toggle span::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #fff;
  transition: transform 0.4s;
}

.header--nav-opened .header-toggle span::before {
  display: block;
  transform: rotate(45deg);
}

.header--nav-opened .header-toggle span::after {
  transform: rotate(-45deg);
}

.main__block {
  margin-bottom: 140px;
}

.about-me {
  padding-top: 140px;
}

.about-me-content {
  display: flex;
  flex-direction: column;
  gap: 60px;
}

@media (min-width: 1200px) {
  .about-me-content {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.about-me-image {
  align-self: center;
  width: 389px;
  height: 389px;
  border-radius: 50%;
  -o-object-position: center;
  object-position: center;
  -o-object-fit: cover;
  object-fit: cover;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}

@media (max-width: 659px) {
  .about-me-image {
    display: none;
  }
}
.about-me-text {
  line-height: 1.64;
  display: flex;
  flex-direction: column;
}

@media (min-width: 1200px) {
  .about-me-text {
    width: 700px;
  }
}
.about-me-text p {
  margin-bottom: 20px;
}

.about-me-title {
  color: #22a39f;
  font-size: 54px;
  font-weight: 400;
  text-align: center;
}

.portfolio {
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.portfolio-title {
  color: #22a39f;
  font-size: 54px;
  font-weight: 400;
  text-align: center;
}

.portfolio-list {
  list-style: none;
}

@media (min-width: 1200px) {
  .portfolio-list {
    display: grid;
    grid-template-columns: minmax(auto, 575px) minmax(auto, 575px);
    gap: 40px;
  }
}
.portfolio-item {
  position: relative;
  margin-bottom: 20px;
  overflow: hidden;
  border-radius: 12px;
  height: 350px;
}

.portfolio-item::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.4;
  z-index: 1;
  pointer-events: none;
}

.portfolio-item:hover::after {
  opacity: 0;
}

@media (min-width: 1200px) {
  .portfolio-item {
    margin-bottom: 0;
  }
}
.portfolio-item-link {
  top: calc(50% - 50px);
  left: calc(50% - 50px);
  position: absolute;
  z-index: 1;
  align-items: center;
  display: flex;
  height: 100px;
  width: 100px;
  font-size: 12px;
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  background-color: #22a39f;
  border-radius: 50%;
  padding: 10px;
  justify-content: center;
  cursor: pointer;
  transform: scale(0);
  transition: transform 0.3s;
}

.portfolio-item-image {
  display: block;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: top;
  object-position: top;
  transform: scale(1);
  transition: transform 0.3s;
}

.portfolio-item:hover .portfolio-item-image {
  transform: scale(1.1);
}

.portfolio-item:hover .portfolio-item-link {
  transform: scale(1);
}

.navigation {
  background-color: #22a39f;
  top: 0;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  right: 0;
  align-items: center;
  padding: 30px;
  transform: translateY(-100%);
  transition: transform 0.3s ease;
}

@media (min-width: 1200px) {
  .navigation {
    padding: 0;
    position: static;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    transform: translateY(0);
  }
}
.header--nav-opened .navigation {
  transform: translateY(0);
  transition: transform 0.2s ease;
}

.navigation-link {
  position: relative;
  display: inline-block;
  padding-top: 10px;
  padding-bottom: 10px;
  color: #fff;
  text-decoration: none;
  font-size: 60px;
  line-height: 100%;
  font-weight: 400;
  transition: color 0.2s ease, opacity 0.3s ease;
  opacity: 0;
}

.navigation-link[href]:hover {
  color: #f3efe0;
}

@media (min-width: 1200px) {
  .navigation-link {
    opacity: 1;
    margin-left: 20px;
    font-size: 26px;
  }
  .navigation-link::after {
    position: absolute;
    content: "";
    height: 2px;
    width: 100%;
    background-color: #fff;
    top: 100%;
    left: 0;
    opacity: 0;
    transform: translateY(10px);
    transition: opacity 0.3s, transform 0.3s;
  }
  .navigation-link--current::after {
    opacity: 1;
    transform: translateY(0);
  }
  .navigation-link:focus::after, .navigation-link:hover::after {
    opacity: 1;
    transform: translateY(0);
  }
}
.header--nav-opened .navigation-link {
  opacity: 1;
}

.header--nav-opened .navigation-link:nth-child(1) {
  transition-property: opacity;
  transition-delay: 0.3s;
}

.header--nav-opened .navigation-link:nth-child(2) {
  transition-delay: 0.45s;
  transition-property: opacity;
}

.footer {
  background-color: #333;
  text-align: center;
  color: #a4a4a4;
  font-size: 18px;
  font-weight: 400;
}

.footer-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 30px;
  padding-top: 30px;
}

.footer-item:not(:last-child) {
  margin-right: 30px;
}

@media (min-width: 1200px) {
  .footer-item:not(:last-child) {
    margin-right: 100px;
  }
}
.footer h3 {
  color: #fff;
  font-weight: 400;
}

.footer-link {
  text-decoration: none;
  color: #fff;
  font-size: 18px;
}

.footer-link:hover {
  color: #22a39f;
}

.slider {
  position: relative;
}

.slider-wrapper {
  position: relative;
  height: 400px;
  overflow: hidden;
  width: 100%;
  margin: 0 auto;
}

@media (min-width: 1200px) {
  .slider-wrapper {
    max-width: 700px;
  }
}
.slider-item {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  transform: translateX(100%);
  transition: transform 0.5s ease;
}

.slider-item--current {
  transform: translateX(0);
  transition: transform 0.5s ease;
}

.slider-btn {
  position: absolute;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  z-index: 1;
  top: 50%;
  background-color: #22A39F;
  border: 0;
  cursor: pointer;
  padding: 5px 10px;
  transform: translateY(-50%);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 12px;
}

.slider-btn--prev {
  left: 2%;
  background-image: url("../img/arrowPrev.svg");
}

.slider-btn--next {
  right: 2%;
  background-image: url("../img/arrowNext.svg");
}

.slider-btn:hover {
  background-color: #1c8784;
}

.slider-btn:disabled {
  opacity: 0.5;
}

.slider-image {
  display: block;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: top;
  object-position: top;
}

